import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import accountSelect from '@/components/common-components/accountSelect';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.raceLength = 0;
        this.raceCost = 0;
        this.accountList = [];
        this.accountId = '';
    }
    // 计算属性
    get computedMsg() {
        return 'computed ' + this.propB;
    }
    get accountOptions() {
        this.accountList.forEach((obj) => {
            obj.value = obj.id;
            obj.text = obj.name;
        });
        return this.accountList;
    }
    getRaceByAccountId() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getRaceByAccountId', { accountId: this.accountId }).then((response) => {
            if (response.success) {
                let resData = response.data;
                if (resData.raceLength) {
                    this.raceLength = resData.raceLength;
                    this.raceCost = resData.raceCost;
                }
            }
            toast.hide();
        });
    }
    getAccountId(id) {
        this.accountId = id;
        this.getRaceByAccountId();
    }
    toOtherPage(type) {
        if (type) {
            let path = '/' + type;
            this.$router.push({ path: path, query: { accountId: this.accountId } });
        }
    }
};
payAdmin = __decorate([
    Component({
        components: {
            accountSelect
        },
        filters: {
            fenToYuan(num) {
                if (typeof num !== 'number' || isNaN(num))
                    return null;
                return '¥' + (num / 100).toFixed(2);
            }
        }
    })
], payAdmin);
export default payAdmin;
